//////////////////////////////////////// REACT COMPONENTS ////////////////////////////////////////////

const changeObject = (object, e) => {
    let returnObject = {
        ...object,
        [e.target.name]: e.target.value
    }
    return returnObject
}



///////////////////////////////////////// SORTING FUNCTIONS /////////////////////////////////////////////////

const bubbleSort = array => {
    let sorted = false;
    while (sorted=== false) {
        sorted = true;
        for(let i = 0; i < array.length -1; i++) {
            if (array[i] > array[i+1]) {
                sorted = false;
                [array[i], array[i+1]] = [array[i+1], array[i]]
            }
        }
    }
    return array
}

const inssertionSort = array => {
    if (array.length <= 1) return array;
    for (let i = 1; i < array.length; i++){
        let j = i;
        while(j > 0 && array[j] < array[j-1]){
            [array[j-1], array[j]] = [array[j], array[j-1]];
            j--;
        }
    }
    return array
}

const merge = (arr1, arr2) => {
    let i = 0;
    let j = 0;
    let newArr = [];
    while(i < arr1.length && j < arr2.length){
        if (arr1[i] < arr2[j]){
            newArr.push(arr1[i]);
            i++
        } else {
            newArr.push(arr2[j]);
            j++
        }
    }
    return newArr.concat(arr1.slice(i)).concat(arr2.slice(j))
}

const mergeSort = arr =>{
    if (arr.length <= 1) {
        return arr;
    }
    let left = mergeSort(arr.slice(0,arr.length/2));
    let right = mergeSort(arr.slice(arr.length/2));
    return (merge(left, right))
}

const mergeObjs = (arr1, arr2, key) => {
    let i = 0;
    let j = 0;
    let newArr = [];
    while(i < arr1.length && j < arr2.length) {
        if (arr1[i][key] < arr2[j][key]) {
            newArr.push(arr1[i]);
            i++;
        } else {
            newArr.push(arr2[j]);
            j++;
        }
    }
    return newArr.concat(arr1.slice(i)).concat(arr2.slice(j))
}

const mergeSortArrObj = (array, key) => {
    if (array.length <= 1) {
        return array;
    }
    let left = mergeSortArrObj(array.slice(0,array.length/2), key);
    let right = mergeSortArrObj(array.slice(array.length/2), key);
    return(mergeObjs(left, right, key))
}

module.exports = {
    changeObject,
    bubbleSort,
    inssertionSort,
    mergeSort,
    mergeSortArrObj
}